.App {
  font-family: -apple-system,
    BlinkMacSystemFont,
    sans-serif !important;
  font-style: normal;
  text-align: center;
  line-height: 20px;
}

.rcw-question-separator {
  border: 1px solid #CDDAE5;
  width: 325px;
}

.rcw-disclaimer-info {
  border: 1px solid #CDDAE5;
  padding: 16px;
  text-align: left;
  float: right;
  font-size: 12px;
  color: #5D7283;
  border-radius: 4px;
}

.rcw-info {
  width: 24px;
  height: 24px;
  float: left;
  padding: 16px 16px 16px 0;
}

.rcw-loading-img {
  height: 32px !important;
}

.rcw-response {
  max-width: 245px !important;
}

.rcw-client {
  background: #0E538B !important;
  border-radius: 16px !important;
}

.rcw-client .rcw-message-text {
  color: white;
}

.rcw-message {
  font-weight: 400;
  font-size: 13px;
  margin: 16px !important;
}

.rcw-conversation-container .rcw-header {
  background-color: white !important;
  color: black !important;
  padding: 16px 0 16px 0 !important;
  text-align: left !important;
  border-bottom: 1px solid #CDDAE5;
}

.rcw-conversation-container .rcw-header .rcw-title {
  margin: 0;
  padding: 0;
  font-weight: 600;
  font-size: 14px;
}

.rcw-conversation-container {
  max-width: 380px !important;
  max-height: 560px !important;
  margin-bottom: 16px !important;

  background: #FFFFFF !important;
  /* Light mode/shadowLow */

  box-shadow: 0px 4px 16px rgba(31, 39, 45, 0.12) !important;
  border-radius: 4px !important;
}

.rcw-send-icon {
  height: 18px !important;
  width: 21px !important;
}

.rcw-send {
  padding: 0 !important;
  background-color: white !important;
}

.rcw-sender {
  border-radius: 0 !important;
  padding: 16px !important;
  height: 32px !important;
  background-color: white !important;
  border-top: 1px solid #CDDAE5;
}

.rcw-new-message {
  padding: 0px !important;
  height: 20px !important; 
  background-color: white !important;
  font-size: 13px !important;
}

.rcw-messages-container {
  padding-top: 0 !important;
}

.rcw-conversation-container .avatar {
  width: 16px !important;
  height: 16px !important;
  padding-left: 24px !important;
  border-radius: 0 !important;
}

.rcw-avatar {
  width: 16px !important;
  height: 16px !important;

  padding: 8px;
  background: #FFF5F7;
}

.rcw-custom-launcher {
  width: 50px;
  height: 50px;
  left: 126px;
  top: 0px;

  align-self: flex-end;
  border: 0;
  border-color: transparent;
  border-radius: 50%;
  background: white;
  box-shadow: 0px 4px 16px rgba(31, 39, 45, 0.12);
}

.rcw-close-launcher {
  padding-top: 6px;
}

.quick-button {
  background: #0E538B !important;
  border-radius: 4px !important;
  border: 0 !important;

  font-weight: 400 !important;
  font-size: 13px !important;
  line-height: 20px !important;
  color: #FFFFFF !important;
}

.rcw-sources-title {
  color: #5D7283;
  margin-block-end: 0;
}

.rcw-link {
  color: #2272B4 !important;
  text-decoration-line: none !important;
  font-size: 13px !important;
}